<template>
  <div class="river-compass">
    <div v-if="step===1" class="compass-1-bg">
      <img class="compass-1-locate" src="@/assets/images/river/river-small-locate.png" alt="ghost">
      <div class="compass-1-text">怪奇佈下結界來隱藏自己，使我們沒辦法知道他們的精確位置。博物館已經找到怪奇可能的所在點，請前往畫面上綠色的「定位點」附近待命。</div>
      <div @click="goTo2" class="compass-1-btn">已抵達</div>
    </div>
    <div v-if="step===2" class="compass-2-bg">
      <img class="compass-2-board" src="@/assets/images/river/river-boss-temple.png" alt="ghost">
      <img class="compass-2-board" src="@/assets/images/river/river-chin-an.png" alt="ghost">
      <img class="compass-2-board" src="@/assets/images/river/river-three-station.png" alt="ghost">
      <div class="compass-2-text">博物館已經偵測出結界的三個端點。請站在『定位點』上，將羅盤對準北方，依序找出這三個地點的方位，破解結界的密碼就會顯現，即可進入結界。請拿出道具包的「怪奇羅盤」定位。</div>
      <div class="compass-2-green-text">請拿出道具包的「怪奇羅盤」定位。</div>
      <input v-model="code" class="compass-2-input" type="text">
      <div @click="go" class="compass-2-btn">GO</div>
    </div>
    <div v-if="step===3" class="compass-3-bg">
      <div class="compass-3-text-box">
        <div>你所在的地方傳來陣陣惡臭，混合魚腥味、汽油味、糞便味的嗅覺刺激撲鼻而來，你感到頭昏腦花。你發現一些市民都昏倒在附近了，如果不趕快處理盤據在這裡的怪奇，大家可能會有危險。</div>
        <div>
          <div @click="nextPage" class="compass-3-triangle"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'RiverHouseCompass',
  components: {

  },
  data () {
    return {
      step: 1,
      code: '',
      isShowFirstAnimal: false,
      isShowSecondAnimal: false,
      isShowThirdAnimal: false,
      isShowFourAnimal: false,
      isShowWrong: false
    }
  },
  mounted () {

  },
  destroyed () {

  },
  methods: {
    goTo2 () {
      this.step = 2
      this.$store.commit('changeHeader', false)
    },
    go () {
      if (this.code.toLowerCase() === 'tpl' || this.code.toLowerCase() === 'wpl' || this.code.toLowerCase() === 'tsl' || this.code.toLowerCase() === 'wsl') {
        alert('答對囉')
        this.step = 3
      } else {
        alert('答錯囉')
      }
    },
    nextPage () {
      this.$router.push({
        path: './river-house'
      })
    }
  },
  computed: {
    // power () {
    //   return this.$store.state.power
    // }
  }
}
</script>
<style lang="scss" scoped>

  .river-compass {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    position: relative;
    padding: 0px;

    .compass {

      &-1 {

        &-bg {
          // width: 100vw;
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/river/river-dot-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;

        }

        &-locate {
          width: 45vw;
          height: auto;
        }

        &-text {
          color: white;
          padding: 2px 8px;
          height: 190px;
          overflow: scroll;
        }

        &-btn {
          width: 100%;
          height: 60px;
          line-height: 60px;
          color: white;
          text-align: center;
          background-color: darkgrey;
        }
      }

      &-2 {

        &-bg {
          // width: 100vw;
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/river/river-dot-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;
          padding: 10px;
          margin: auto;
        }

        &-board {
          margin-top: 10px;
          width: 100vw;
          height: auto;
          line-height: 40px;
          text-align: center;
          color: black;

          &:first-child {
            margin-top: 20px;
          }
        }

        &-text {
          color: white;
          padding: 4px 10px;
          font-size: 14px;
        }

        &-green-text {
          color:#95ff00;
          padding: 10px;
        }

        &-input {
            width: 150px;
            padding: 4px;
            color: black;
        }

        &-btn {
          width: 50px;
          height: 50px;
          line-height: 50px;
          text-align: center;
          background: #95ff00;
          color: black;
          margin-top: 10px;
          border-radius: 100%;
        }
      }

      &-3 {

        &-bg {
          height: 100vh;
          left: 0;
          top: 0;
          opacity: 0.8;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          background-image: url('../../assets/images/river/river-black-map.png');
          background-size: cover;
          background-position-y: center;
          background-repeat: no-repeat;
          background-position-x: center;
          padding: 10px;
          margin: auto;
        }

        &-text-box {
          min-height: 150px;
          padding: 20px;
          background-color: #95ff00;
          opacity: 0.8;
          margin-top: 160px;
          color: #000;
        }

        &-triangle {
          margin: 20px auto 0px;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 20px 20px 0 20px;
          border-color: black transparent transparent transparent;
        }
      }

    }
  }

</style>



